::-webkit-scrollbar {
  width: 4px;
  height: 168px;
  border-radius: 6px;
}

::-webkit-scrollbar-track {
  background: #d7e3ff;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb {
  background: #9ebbff;
}

.todayWaterListSection {
  background-color: #fff;
  max-width: 264px;
  box-shadow: 0 4px 14px 0 rgba(64, 123, 255, 0.3);
  background: #ecf2ff;
  border-radius: 10px;
  padding: 24px 8px;
  width: 100%;
  height: auto;
  display: block;
  position: relative;
  margin: 40px auto;
 }



.title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #333;
  margin: 0;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 180px;
  overflow-y: auto;
}

.listItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #e0e0e0;
  transition: background-color 0.2s;
}

.listItem:hover {
  background-color: #f9f9f9;
}

.info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.amount {
  font-size: 1rem;
  font-weight: 400;
  color: #407BFF;
  display: flex;
  align-items: center;
  gap: 8px;
}

.amountIcon {
  height: 26px;
  width: 26px;
  fill: #407BFF;
}

.time {
  font-size: 0.9rem;
  color: #666;
}

.actions {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.editButton,
.deleteButton {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 8px;
  transition: background-color 0.2s;
}

.editButton:hover {
  background-color: rgba(74, 144, 226, 0.1);
}

.deleteButton:hover {
  background-color: rgba(255, 76, 76, 0.1);
}

.addButton {
  margin-top: 16px;
  display: flex;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.2s;
  background: none;
  border: none;
  outline: none;
  box-shadow: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: #407bff;
}

.addButton:hover {
  color: #ff9d43;
}

.twbtni {
  fill: #4a90e2;
}

.editButton {
  stroke-width: 1px;
  stroke: #9ebbff;
}

@media screen and (min-width: 768px) {
  .todayWaterListSection {
    padding: 32px 24px;
    padding: 32px 24px;
    max-width: 656px;
    height:auto;
    /* margin: 0 32px; */

  }
}

@media screen and (min-width: 1440px) {
  .todayWaterListSection {
    border-radius: 10px;
    padding: 32px 24px;
    width: 592px;
  }

  .title {
    margin-bottom: 12px;
  }
}
